/*
 verifica_cpf_cnpj
 
 Verifica se  CPF ou CNPJ
 
 @see http://www.todoespacoonline.com/w/
*/
function verifica_cpf_cnpj ( valor ) {

  // Garante que o valor  uma string
  valor = valor.toString();
  
  // Remove caracteres invlidos do valor
  valor = valor.replace(/[^0-9]/g, '');

  // Verifica CPF
  if ( valor.length === 11 ) {
      return 'CPF';
  } 
  
  // Verifica CNPJ
  else if ( valor.length === 14 ) {
      return 'CNPJ';
  } 
  
  // No retorna nada
  else {
      return false;
  }
  
} // verifica_cpf_cnpj

/*
calc_digitos_posicoes

Multiplica dgitos vezes posies

@param string digitos Os digitos desejados
@param string posicoes A posio que vai iniciar a regresso
@param string soma_digitos A soma das multiplicaes entre posies e dgitos
@return string Os dgitos enviados concatenados com o ltimo dgito
*/
function calc_digitos_posicoes( digitos, posicoes = 10, soma_digitos = 0 ) {

  // Garante que o valor  uma string
  digitos = digitos.toString();

  // Faz a soma dos dgitos com a posio
  // Ex. para 10 posies:
  //   0    2    5    4    6    2    8    8   4
  // x10   x9   x8   x7   x6   x5   x4   x3  x2
  //   0 + 18 + 40 + 28 + 36 + 10 + 32 + 24 + 8 = 196
  for ( var i = 0; i < digitos.length; i++  ) {
      // Preenche a soma com o dgito vezes a posio
      soma_digitos = soma_digitos + ( digitos[i] * posicoes );

      // Subtrai 1 da posio
      posicoes--;

      // Parte especfica para CNPJ
      // Ex.: 5-4-3-2-9-8-7-6-5-4-3-2
      if ( posicoes < 2 ) {
          // Retorno a posio para 9
          posicoes = 9;
      }
  }

  // Captura o resto da diviso entre soma_digitos dividido por 11
  // Ex.: 196 % 11 = 9
  soma_digitos = soma_digitos % 11;

  // Verifica se soma_digitos  menor que 2
  if ( soma_digitos < 2 ) {
      // soma_digitos agora ser zero
      soma_digitos = 0;
  } else {
      // Se for maior que 2, o resultado  11 menos soma_digitos
      // Ex.: 11 - 9 = 2
      // Nosso dgito procurado  2
      soma_digitos = 11 - soma_digitos;
  }

  // Concatena mais um dgito aos primeiro nove dgitos
  // Ex.: 025462884 + 2 = 0254628842
  var cpf = digitos + soma_digitos;

  // Retorna
  return cpf;
  
} // calc_digitos_posicoes

/*
Valida CPF

Valida se for CPF

@param  string cpf O CPF com ou sem pontos e trao
@return bool True para CPF correto - False para CPF incorreto
*/
function valida_cpf( valor ) {

  // Garante que o valor  uma string
  valor = valor.toString();
  
  // Remove caracteres invlidos do valor
  valor = valor.replace(/[^0-9]/g, '');


  // Captura os 9 primeiros dgitos do CPF
  // Ex.: 02546288423 = 025462884
  var digitos = valor.substr(0, 9);

  // Faz o clculo dos 9 primeiros dgitos do CPF para obter o primeiro dgito
  var novo_cpf = calc_digitos_posicoes( digitos );

  // Faz o clculo dos 10 dgitos do CPF para obter o ltimo dgito
  var novo_cpf = calc_digitos_posicoes( novo_cpf, 11 );

  // Verifica se o novo CPF gerado  idntico ao CPF enviado
  if ( novo_cpf === valor ) {
      // CPF vlido
      return true;
  } else {
      // CPF invlido
      return false;
  }
  
} // valida_cpf

/*
valida_cnpj

Valida se for um CNPJ

@param string cnpj
@return bool true para CNPJ correto
*/
function valida_cnpj ( valor ) {

  // Garante que o valor  uma string
  valor = valor.toString();
  
  // Remove caracteres invlidos do valor
  valor = valor.replace(/[^0-9]/g, '');

  
  // O valor original
  var cnpj_original = valor;

  // Captura os primeiros 12 nmeros do CNPJ
  var primeiros_numeros_cnpj = valor.substr( 0, 12 );

  // Faz o primeiro clculo
  var primeiro_calculo = calc_digitos_posicoes( primeiros_numeros_cnpj, 5 );

  // O segundo clculo  a mesma coisa do primeiro, porm, comea na posio 6
  var segundo_calculo = calc_digitos_posicoes( primeiro_calculo, 6 );

  // Concatena o segundo dgito ao CNPJ
  var cnpj = segundo_calculo;

  // Verifica se o CNPJ gerado  idntico ao enviado
  if ( cnpj === cnpj_original ) {
      return true;
  }
  
  // Retorna falso por padro
  return false;
  
} // valida_cnpj

/*
valida_cpf_cnpj

Valida o CPF ou CNPJ

@access public
@return bool true para vlido, false para invlido
*/
function valida_cpf_cnpj ( valor ) {

  // Verifica se  CPF ou CNPJ
  var valida = verifica_cpf_cnpj( valor );

  // Garante que o valor  uma string
  valor = valor.toString();
  
  // Remove caracteres invlidos do valor
  valor = valor.replace(/[^0-9]/g, '');


  // Valida CPF
  if ( valida === 'CPF' ) {
      // Retorna true para cpf vlido
      return valida_cpf( valor );
  } 
  
  // Valida CNPJ
  else if ( valida === 'CNPJ' ) {
      // Retorna true para CNPJ vlido
      return valida_cnpj( valor );
  } 
  
  // No retorna nada
  else {
      return false;
  }
  
} // valida_cpf_cnpj

/*
formata_cpf_cnpj

Formata um CPF ou CNPJ

@access public
@return string CPF ou CNPJ formatado
*/
function formata_cpf_cnpj( valor ) {

  // O valor formatado
  var formatado = false;
  
  // Verifica se  CPF ou CNPJ
  var valida = verifica_cpf_cnpj( valor );

  // Garante que o valor  uma string
  valor = valor.toString();
  
  // Remove caracteres invlidos do valor
  valor = valor.replace(/[^0-9]/g, '');


  // Valida CPF
  if ( valida === 'CPF' ) {
  
      // Verifica se o CPF  vlido
      if ( valida_cpf( valor ) ) {
      
          // Formata o CPF ###.###.###-##
          formatado  = valor.substr( 0, 3 ) + '.';
          formatado += valor.substr( 3, 3 ) + '.';
          formatado += valor.substr( 6, 3 ) + '-';
          formatado += valor.substr( 9, 2 ) + '';
          
      }
      
  }
  
  // Valida CNPJ
  else if ( valida === 'CNPJ' ) {
  
      // Verifica se o CNPJ  vlido
      if ( valida_cnpj( valor ) ) {
      
          // Formata o CNPJ ##.###.###/####-##
          formatado  = valor.substr( 0,  2 ) + '.';
          formatado += valor.substr( 2,  3 ) + '.';
          formatado += valor.substr( 5,  3 ) + '/';
          formatado += valor.substr( 8,  4 ) + '-';
          formatado += valor.substr( 12, 14 ) + '';
          
      }
      
  } 

  // Retorna o valor 
  return formatado;
  
} // formata_cpf_cnpj

function dateFormat(date) {
  var formattedDate = new Date(parseDate(date));
  var d = formattedDate.getDate();
  var m =  formattedDate.getMonth();
  m += 1;  // JavaScript months are 0-11
  var y = formattedDate.getFullYear();
  if(d < 10) d = "0" + d;
  if(m < 10) m = "0" + m;
  return d + "/" + m + "/" + y;
}

function parseDate(input) {
  var parts = input.match(/(\d+)/g);
  return new Date(parts[0], parts[1]-1, parts[2]); // months are 0-based
}

function fallbackCopyTextToClipboard(text) {
  var textArea = document.createElement("textarea");
  textArea.value = text;
  
  // Avoid scrolling to bottom
  textArea.style.top = "0";
  textArea.style.left = "0";
  textArea.style.position = "fixed";

  document.body.appendChild(textArea);
  textArea.focus();
  textArea.select();

  try {
    var successful = document.execCommand('copy');
    var msg = successful ? 'successful' : 'unsuccessful';
    console.log('Fallback: Copying text command was ' + msg);
  } catch (err) {
    console.error('Fallback: Oops, unable to copy', err);
  }

  document.body.removeChild(textArea);
}
function copyTextToClipboard(text) {
  if (!navigator.clipboard) {
    fallbackCopyTextToClipboard(text);
    return;
  }
  navigator.clipboard.writeText(text).then(function() {
    console.log('Async: Copying to clipboard was successful!');
  }, function(err) {
    console.error('Async: Could not copy text: ', err);
  });
}