function sortTable(n, id) {
  var table, rows, switching, i, x, y, shouldSwitch, dir, switchcount = 0;
  table = document.getElementById(id);
  switching = true;
  dir = "asc";

  while (switching) {
    var a, b;
    switching = false;
    rows = table.rows;

    for (i = 1; i < (rows.length - 1); i++) {
      shouldSwitch = false;
      x = rows[i].getElementsByTagName("td")[n];
      y = rows[i + 1].getElementsByTagName("td")[n];
      a = x.innerHTML.replace(',', '');
      b = y.innerHTML.replace(',', '');
      if (isNaN(a) && isNaN(b)) {
        if (dir == "asc") {
          if (a.toLowerCase() > b.toLowerCase()) {
            shouldSwitch = true;
            break;
          }
        } else if (dir == "desc") {
          if (a.toLowerCase() < b.toLowerCase()) {
            shouldSwitch = true;
            break;
          }
        }
      } else {
        if (dir == "asc") {
          if (parseFloat(a) > parseFloat(b)) {
            shouldSwitch = true;
            break;
          }
        } else if (dir == "desc") {
          if (parseFloat(a) < parseFloat(b)) {
            shouldSwitch = true;
            break;
          }
        }
      }
    }
    if (shouldSwitch) {
      rows[i].parentNode.insertBefore(rows[i + 1], rows[i]);
      switching = true;
      switchcount++;
    } else {
      if (switchcount == 0 && dir == "asc") {
        dir = "desc";
        switching = true;
      }
    }
  }
  return 0;
}

// Sort tables by clicking on it's header columns.
var table = document.getElementById('tabela');
if(table) {
  table.querySelectorAll('th').forEach((th, pos) => {
    th.addEventListener('click', evt => sortTable(pos, 'tabela'));
  });
}
